

@import "_font.scss";


$font-text:"MyriadProRegular";
$font-title: "Segoe UI Semibold";
@mixin padding_margin ($pad,$marg){
    padding: $pad;
    margin: $marg;
}
@mixin text__size-weight-family-color ($size,$weight,$family,$color){   
    font-size: $size ;  
    font-weight: $weight !important;
    font-family:$family;
    color:$color;     
    vertical-align: baseline;
}
@mixin flex ($aligin,$justify,$direction,$wrap_nowrap){
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;    
    align-items: $aligin;    
    justify-content: $justify;    
    flex-direction: $direction;   
    flex-wrap: $wrap_nowrap;
}


@import "_header.scss";
@import "_slider.scss";
@import "_company.scss";
@import "_features.scss";
@import "_footer.scss";

*{    
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}
body{ 
    background: rgb(255, 255, 255);
    
}
img {
    max-width: 100%;
    height: auto;
}


